<template>
    <div>
        <Row type="flex" justify="end" class="code-row-bg">
            <Col span="8">
                <Input search enter-button placeholder="请输入关键字搜索" style="width: 300px;float: right;" @on-search="handleSearch" v-model="search"/>
            </Col>
        </Row>
        <Row type="flex" justify="start" class="code-row-bg" style="padding: 10px;">
            <Col span="24">
                <Table border :columns="columns" :data="sell_items_data" @on-sort-change="sortChange"></Table>
            </Col>
        </Row>
        <Row type="flex" justify="end" class="code-row-bg">
            <Col span="24">
                <Page :total="total_count" show-total show-sizer :current="page" :page-size="page_size" :page-size-opts=[20,50,100,500] @on-change="changePage" @on-page-size-change="changePageSize" style="float: right;"/>
            </Col>
        </Row>
    </div>
</template>
<script>
const baseAPIUrl = process.env.baseAPIUrl;
    export default {
        name: "SellItems",
        data () {
            return {
                search: '',
                page: 1,
                page_size: 20,
                total_count: 0,
                order: "desc",
                order_key: "created_time",
                sell_items_data: [],
                columns: [
                    {
                        title: '编号',
                        key: 'raw_id',
                        width: 80
                    },
                    {
                        title: '单号',
                        key: 'item_number',
                        sortable: 'custom'
                    },
                    {
                        title: '发型师',
                        key: 'hairdresser',
                        sortable: 'custom'
                    },
                    {
                        title: '助理',
                        key: 'assistant',
                        sortable: 'custom'
                    },
                    {
                        title: '消费类型',
                        key: 'item_type',
                        sortable: 'custom'
                    },
                    {
                        title: '金额',
                        key: 'money',
                        sortable: 'custom'
                    },
                    {
                        title: '付款类型',
                        key: 'pay_type',
                        sortable: 'custom'
                    },
                    {
                        title: '会员',
                        key: 'fellow',
                        sortable: 'custom'
                    },
                    {
                        title: '开单时间',
                        key: 'created_time',
                        sortable: 'custom'
                    },
                    {
                        title: 'Action',
                        key: 'action',
                        width: 150,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.show(params.index)
                                        }
                                    }
                                }, '编辑'),
                                // h('Button', {
                                //     props: {
                                //         type: 'error',
                                //         size: 'small'
                                //     },
                                //     on: {
                                //         click: () => {
                                //             this.remove(params.index)
                                //         }
                                //     }
                                // }, '删除')
                            ]);
                        }
                    }
                ]
            }
        },
        computed: {
        },
        created: function() {
            this.getSellItems();
        },
        methods: {
            getSellItems() {
                var post_URL = baseAPIUrl + "sell_items?";
                post_URL += "page=" + this.page;
                post_URL += "&page_size=" + this.page_size;
                post_URL += "&order=" + this.order;
                post_URL += "&order_key=" + this.order_key;

                this.$http.post(post_URL, {search: this.search}).then(response => {
                    const res = response.data;
                    this.sell_items_data = res['data'];
                    this.total_count = res['total_count'];
                    this.page = res['page'];
                    if(res['status'] != "success")
                        this.$Message.error(res['message']);
                }, response => {
                    if(response.status == 401){
                      this.$Message.error('请登陆');
                      this.$router.push({
                        name: "login"
                      });
                    }
                });
            },
            handleSearch(value) {
                this.search = value;
                this.getSellItems();
            },
            show (index) {
                this.$Modal.info({
                    title: 'User Info',
                    content: `Name：${this.sell_items_data[index].name}<br>Age：${this.sell_items_data[index].age}<br>Address：${this.sell_items_data[index].address}`
                })
            },
            remove (index) {
                this.sell_items_data.splice(index, 1);
            },
            changePage(page) {
                this.page = page;
                this.getSellItems();
            },
            changePageSize(page_size) {
                this.page_size = page_size;
                this.getSellItems();
            },
            sortChange(value){
                this.order = value.order;
                this.order_key = value.key;
                this.getSellItems();
            }
        }
    }
</script>



